import React from "react";

export default function Home() {
  return (
    <div>
      <div className="homeHeader">
        <img
          src="https://drive.google.com/uc?id=19PJfrUONht7lHaVLxWHr_a3hCR4FCNMg"
          alt="Home"
        />
        <div className="header">
          <h1>Adopt a Pet Today!</h1>
          <p>
            Pet Heaven is an animal welfare charity society which aims to
            improve the welfare of rescue and abandoned pets in Singapore.
          </p>
        </div>
        <div className="homeInfo">
          <p>
            Our mission is to increase public awareness, eliminate animal
            cruelty and abandonment, provide shelter for these animals and
            helping them to look for a home.
            <br />
            While we accept request to release pets, we do not encourage such
            behaviour.
            <br />
            We hope you will give a chance for these innocent animals to finally
            have a warm home and loving family to call its own!
          </p>
        </div>
        <div className="adoptProcess">
          <h1>Adoption Process</h1>
          <p>
            If you are interested in adopting one of these furry kids, proceed
            to submit an application form and our administrator will evaluate
            your application and get back to you.
          </p>
        </div>
      </div>
    </div>
  );
}
