import React, { useState } from "react";
import Pet from "./Pet";
import pets from "../pets";
import { NavLink } from "react-router-dom";
import PetsIcon from "@mui/icons-material/Pets";

export default function PetList() {
  const [search, setSearch] = useState("");
  const [searchInput] = useState(["name", "gender", "breed", "age", "color"]);
  const [filterSearch, setFilterSearch] = useState(["All"]);
  const data = Object.values(pets);

  function searchPet(pets) {
    return pets.filter((pet) => {
      if (pet.type == filterSearch) {
        return searchInput.some((newPet) => {
          return (
            pet[newPet].toString().toLowerCase().indexOf(search.toLowerCase()) >
            -1
          );
        });
      } else if (filterSearch == "All") {
        return searchInput.some((newPet) => {
          return (
            pet[newPet].toString().toLowerCase().indexOf(search.toLowerCase()) >
            -1
          );
        });
      }
    });
  }
  return (
    <div className="petHeader">
      <h1>
        Pets Looking For A Home <PetsIcon />
      </h1>
      <div className="searchContainer">
        <div className="searchBar">
          <input
            className="search"
            type="text"
            name="searchBar"
            id="searchBar"
            placeholder="Search..."
            value={search}
            onChange={(e) => setSearch(e.target.value)}
          />
        </div>
        <div className="selectBar">
          <select
            onChange={(e) => {
              setFilterSearch(e.target.value);
            }}
          >
            <option value="All">Filter By Type</option>
            <option value="Cat">Cat</option>
            <option value="Dog">Dog</option>
          </select>
        </div>
      </div>
      <div className="cardContainer">
        <ul className="cardGrid">
          {searchPet(data).map((eachPet) => (
            <NavLink className="cardLink" to="/Adopt">
              <li>
                <Pet
                  key={eachPet.id}
                  name={eachPet.name}
                  gender={eachPet.gender}
                  type={eachPet.type}
                  breed={eachPet.breed}
                  color={eachPet.color}
                  image={eachPet.image}
                  age={eachPet.age}
                />
              </li>
            </NavLink>
          ))}
        </ul>
      </div>
    </div>
  );
}
