import React from "react";
import { useState } from "react";
import { NavLink } from "react-router-dom";
import Modal from "@mui/material/Modal";
import Box from "@mui/material/Box";

const style = {
  position: "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  marginRight: "-25%",
  bgcolor: "#10316b",
  border: "1px solid black",
  boxShadow: 18,
  p: 4
};

export default function Register() {
  const [input, setInput] = useState({
    name: "",
    email: ""
  });

  function handleChange(event) {
    const { name, value } = event.target;
    setInput((prevValue) => {
      return {
        ...prevValue,
        [name]: value
      };
    });
  }
  const [open, setOpen] = useState(false);
  const handleClose = () => setOpen(false);
  const handleOpen = (event) => {
    event.preventDefault();
    setOpen(true);
  };
  return (
    <div className="regHeader">
      <h1>Register as a member and support Pet Heaven society</h1>
      <div>
        <form className="regForm">
          <div className="item">
            <p>Name</p>
            <input
              type="text"
              onChange={handleChange}
              name="name"
              value={input.name}
              placeholder="Name"
            />
          </div>
          <br />
          <div className="item">
            <p>Email</p>
            <input
              type="email"
              onChange={handleChange}
              name="email"
              placeholder="petheaven@gmail.com"
              value={input.email}
            />
          </div>
          <br />
          <div>
            <button
              className="submitBtn"
              style={{ color: "white" }}
              onClick={handleOpen}
            >
              Register
            </button>
            <Modal open={open}>
              <Box sx={style}>
                <div className="regInfo">
                  <p>
                    Thank you {input.name} for registering and for showing your
                    support for the Pet Heaven society!
                  </p>
                  <p>
                    Updates will be sent to your registered email {input.email}.
                  </p>
                </div>
                <div className="btnContainer">
                  <button onClick={handleClose}>Close</button>
                  <button>
                    <NavLink
                      style={{ textDecoration: "none", color: "black" }}
                      to="/PetList"
                    >
                      Adopt
                    </NavLink>
                  </button>
                </div>
              </Box>
            </Modal>
          </div>
        </form>
      </div>
    </div>
  );
}
