import React from "react";
import { useState } from "react";
import pets from "../pets";
import Mail from "./Mail";

export default function Adopt() {
  const [input, setInput] = useState({
    fName: "",
    lName: "",
    email: "",
    street: "",
    unit: "",
    postal: ""
  });

  const [petName, setPetName] = useState("");

  function handleChange(event) {
    const { name, value } = event.target;
    setInput((prevValue) => {
      return {
        ...prevValue,
        [name]: value
      };
    });
  }

  function handleName(event) {
    setPetName(event.target.value);
  }

  return (
    <div className="adoptPage">
      <div className="adoptHeader">
        <img
          src="https://drive.google.com/uc?id=1Mmtj6hr9xN97SE8C8unn7XWDmkrb7d8q"
          alt="logo"
        />
        <h1>Pet Adoption Application Form</h1>
      </div>
      <hr />
      <form className="adoptForm">
        <div className="item">
          <p>Pet Name</p>
          <select>
            {pets.map((eachPet) => (
              <option onChange={handleName} value={eachPet.name}>
                {eachPet.name}
              </option>
            ))}
          </select>
        </div>
        <div className="item">
          <p>Name</p>
          <input
            type="text"
            onChange={handleChange}
            value={input.fName}
            name="fName"
            placeholder="First"
          />
          <input
            type="text"
            onChange={handleChange}
            value={input.lName}
            name="lName"
            placeholder="Last"
          />
        </div>
        <div className="item">
          <p>Email</p>
          <input
            type="email"
            onChange={handleChange}
            value={input.email}
            name="email"
            placeholder="petheaven@gmail.com"
          />
        </div>
        <div className="item address">
          <div>
            <p>Address</p>
            <div className="street">
              <input
                className="street-item"
                type="text"
                name="street"
                onChange={handleChange}
                value={input.street}
                placeholder="Street address"
              />
            </div>
            <input
              type="text"
              name="unit"
              onChange={handleChange}
              value={input.unit}
              placeholder="Unit"
            />
            <input
              type="text"
              onChange={handleChange}
              value={input.postal}
              name="postal"
              placeholder="Postal Code"
            />
          </div>
        </div>
        <div class="item">
          <p>Reason for Adoption</p>
          <div class="applicationItem">
            <textarea rows="5"></textarea>
          </div>
        </div>
        <small>
          By submitting you declare that all information entered are accurate.
        </small>
        <div className="btnBlock">
          <button className="submitBtn">
            <Mail
              email="admin@petheaven.com"
              subject={petName + "Adoption"}
              body={
                "Name: " +
                input.fName +
                " " +
                input.lName +
                " || Email: " +
                input.email +
                " || Address: " +
                input.street +
                ", " +
                input.unit +
                ", " +
                input.postal
              }
            >
              Submit
            </Mail>
          </button>
        </div>
      </form>
    </div>
  );
}
