import React from "react";
import { useState } from "react";
import Mail from "./Mail";

export default function Reelase() {
  const [input, setInput] = useState({
    petName: "",
    petImg: "",
    fName: "",
    lName: "",
    email: "",
    street: "",
    unit: "",
    postal: ""
  });

  function handleChange(event) {
    const { name, value } = event.target;
    setInput((prevValue) => {
      return {
        ...prevValue,
        [name]: value
      };
    });
  }

  return (
    <div className="adoptPage">
      <div className="adoptHeader">
        <img
          src="https://drive.google.com/uc?id=1Mmtj6hr9xN97SE8C8unn7XWDmkrb7d8q"
          alt="logo"
        />
        <h1>Pet Release Request Form</h1>
      </div>
      <hr />
      <form className="adoptForm">
        <div className="item">
          <p>Pet Name</p>
          <input
            type="text"
            name="petName"
            onChange={handleChange}
            value={input.petName}
          />
        </div>
        <div className="item">
          <p>Pet Image</p>
          <input
            type="file"
            onChange={handleChange}
            value={input.petImg}
            id="img"
            name="img"
            accept="image/*"
          />
        </div>
        <div class="item">
          <p>Name</p>
          <input
            type="text"
            onChange={handleChange}
            value={input.fName}
            name="fName"
            placeholder="First"
          />
          <input
            type="text"
            onChange={handleChange}
            value={input.lName}
            name="lName"
            placeholder="Last"
          />
        </div>
        <div class="item">
          <p>Email</p>
          <input
            type="email"
            onChange={handleChange}
            value={input.email}
            name="email"
            placeholder="petheaven@gmail.com"
          />
        </div>
        <div className="item address">
          <div>
            <p>Address</p>
            <div className="street">
              <input
                className="street-item"
                type="text"
                name="street"
                onChange={handleChange}
                value={input.street}
                placeholder="Street address"
              />
            </div>
            <input
              type="text"
              name="unit"
              onChange={handleChange}
              value={input.unit}
              placeholder="Unit"
            />
            <input
              type="text"
              onChange={handleChange}
              value={input.postal}
              name="postal"
              placeholder="Postal Code"
            />
          </div>
        </div>
        <div class="item">
          <p>Reason for Releasing</p>
          <div class="applicationItem">
            <textarea rows="5"></textarea>
          </div>
        </div>
        <small>
          By submitting you declare that all information entered are accurate.
        </small>
        <div className="btnBlock">
          <button className="submitBtn">
            <Mail
              email="admin@petheaven.com"
              subject="Releasing Pet"
              body={
                "Pet Name: " +
                input.petName +
                "|| Pet Image: " +
                input.petImg +
                "|| Name: " +
                input.fName +
                " " +
                input.lName +
                " || Email: " +
                input.email +
                " || Address: " +
                input.street +
                ", " +
                input.unit +
                ", " +
                input.postal
              }
            >
              Submit
            </Mail>
          </button>
        </div>
      </form>
    </div>
  );
}
